import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ByProductLazadaComponent } from './by-product-lazada.component';
import { FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
const routes: Routes = [
  {
    path: '',
    component: ByProductLazadaComponent
  },

]

@NgModule({
  declarations: [ByProductLazadaComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    FormsModule
  ]
})
export class ByProductLazadaModule { }
